.bg-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  transform: skewY(-3deg);
}

.clip-sides {
  clip-path: polygon(0 -100%, 100% -100%, 100% 200%, 0 200%);
}

.rotation-box {
  width: calc(100% + 30px);
  margin-left: -15px;
  padding-inline: 15px;
  transform: rotate(-3deg);
}

.project-hero {
  background: linear-gradient(-5deg, #edead1 0%, #ffaa2a 84%);
}

.project-hero .content-stripe {
  z-index: 0;
  min-height: 460px;
  padding-bottom: 100px;
  position: relative;
}

.project-cover-image {
  z-index: -5;
  clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);
  background-color: #fff;
  width: 60%;
  height: 100%;
  position: absolute;
}

.project-cover-image img {
  object-fit: cover;
  object-position: left;
  opacity: .8;
  width: 100%;
  height: 100%;
}

.type-badge {
  align-items: center;
  padding: 15px;
  display: flex;
}

.type-badge svg {
  fill: teal;
  stroke: gray;
  width: 32px;
  height: 32px;
}

.type-badge span {
  z-index: -5;
  padding: 3px;
  background-color: #ddd;
  border: 2px solid #23a300;
  margin: -10px;
  padding-inline: 10px 7px;
  font-size: .8em;
}

.project-headline {
  grid-template-columns: 1fr 1fr;
  justify-items: end;
  padding-inline: 50px;
  display: grid;
}

.project-cover-name {
  text-align: right;
  color: #fff;
  text-shadow: 0 0 3px #686868;
  font-size: .8em;
}

.project-cover-name h2 {
  color: #524f4c;
  margin-right: -30px;
}

.project-cover-name h2 span {
  color: #000;
  font-size: 1.1em;
  font-weight: bold;
}

.project-headline .deadline {
  font-weight: 500;
}

.project-description {
  justify-content: flex-end;
  margin-bottom: 30px;
  display: flex;
}

.project-description p {
  text-align: justify;
  width: 35%;
  margin-right: 20px;
}

main {
  margin-top: -50px;
}

.milestones {
  position: relative;
}

.milestones .bg-cover {
  background-color: #fff;
}

.milestones .rotation-box {
  background-color: #e9e9e9;
  border-top: 3px solid #000;
}

.milestones-images {
  justify-content: space-evenly;
  margin-top: -40px;
  display: flex;
}

.milestones-images li img {
  object-fit: cover;
  border: 4px solid #b1b1b19a;
  border-radius: 50%;
  outline: 2px solid #81818161;
  width: 80px;
  height: 80px;
  display: inline-block;
  box-shadow: 2px 4px 5px gray;
}

.milestones-images .in-progress img {
  border-color: #1eb81e;
}

.milestones-images .done {
  filter: grayscale();
}

.next-milestone {
  padding: 10px 100px;
}

.next-milestone h2 {
  font-weight: bold;
  display: inline-block;
}

.overall {
  text-align: right;
  padding: 0 20px 10px 0;
}

.resources {
  margin-top: 100px;
}

.resources .headline {
  margin: 20px;
}

.resources-icons {
  gap: 15px;
  margin: 30px 50px 20px;
  display: flex;
}

.resources-icons label {
  cursor: pointer;
  border-color: #0000;
  border-bottom-style: solid;
  border-bottom-width: 4px;
  padding: 10px;
  display: block;
}

.resources-icons input:checked + label {
  border-color: green;
}

.priority {
  background: linear-gradient(169deg, #d983d2 40%, #006c97 60%);
  justify-content: space-between;
  padding: 5px 15px;
  display: flex;
  box-shadow: 0 0 3px #afafaf;
}

.priority span {
  font-weight: 500;
}

.priority span:last-child {
  color: #fff;
}

.resources-details {
  background-color: #f1f1f1;
  border: 1px solid gray;
  grid-template-columns: 1fr 2fr;
  gap: 30px 15px;
  margin: 20px 10px;
  padding: 20px;
  display: grid;
}

.resource-info {
  grid-template-columns: 1fr 2fr;
  margin-bottom: 20px;
  display: grid;
}

.resource-info > span {
  font-weight: 700;
}

.resource-notes {
  min-height: 300px;
}

.resource-notes h2 {
  text-transform: capitalize;
  margin-bottom: 15px;
}

.resource-notes > span {
  margin-bottom: 15px;
  margin-left: 20px;
  font-weight: 700;
  display: inline-block;
}

.resource-notes p {
  text-align: justify;
  letter-spacing: .02em;
  line-height: 1.4em;
}

.resource-contribution-input {
  display: flex;
}

input#contribution-amount {
  text-align: right;
}

.resource-contribution {
  grid-column: 1 / 2;
}

.require-login {
  color: tomato;
  width: 60%;
  margin: 10px;
  font-style: italic;
}

.submit-button-wrapper {
  height: 50px;
  position: relative;
}

.submit-btn {
  z-index: 5;
  clip-path: polygon(0 0, 100% 0, 100% 65%, 40% 100%, 0 80%);
  border: 2px solid #155600;
  border-bottom: 0;
  justify-content: center;
  width: 75%;
  height: 150px;
  font-size: 2em;
  display: flex;
  position: absolute;
  top: 0;
  left: -31px;
}

.submit-btn span {
  margin-top: 25px;
}

.submit-btn:disabled {
  filter: grayscale();
}

.resource-contribution-acknowledgement {
  align-items: flex-end;
  display: flex;
}

input#contribution-ack {
  margin: 0 5px 2px 0;
  display: inline;
}

#map {
  z-index: 0;
  background: linear-gradient(330deg, #e9e9e9 0%, #adadad 100%);
  width: 100%;
  height: 600px;
}

.leaflet-tile-pane {
  filter: grayscale();
}

.leaflet-popup-content p {
  font-weight: bold;
}

.contributors {
  margin-top: -45px;
  position: relative;
}

.contributors .bg-cover {
  background-color: #e18000;
  justify-content: flex-end;
  display: flex;
}

.bg-cover .stretch {
  height: calc(100% + 10px);
  position: relative;
  top: -5px;
}

.btn-pseudo-bg-front {
  transform-origin: bottom;
  background-color: orange;
  border: 5px solid #7b4101;
  border-right: none;
  width: 50px;
  transform: skewX(352deg);
}

.btn-pseudo-bg-tail {
  border: 5px solid #7b4101;
  background-color: orange;
  border-inline: none;
  width: calc(50% - 312px);
}

.contributors .rotation-box {
  justify-content: space-between;
  min-height: 150px;
  display: flex;
}

.content-stripe .header {
  background-color: #f7bc3d;
  border: 2px solid #a76a00;
  height: 65px;
  padding: 15px 30px;
  position: absolute;
  top: -30px;
}

.last-contributors {
  background-color: #e18000;
  justify-content: space-around;
  width: 75%;
  padding: 10px;
  display: flex;
}

.last-contributors li {
  text-align: center;
  width: 120px;
  padding: 10px;
}

.last-contributors img {
  border-radius: 50%;
  width: 100%;
  margin-bottom: 10px;
  display: block;
}

.last-contributors .name {
  font-size: 1.2em;
  font-style: italic;
}

li.contributors-user-info {
  width: auto;
  display: flex;
}

.contributors-user-info p {
  align-self: center;
  font-size: large;
  font-style: italic;
}

.all-contributors {
  background-color: #0000;
  align-self: flex-end;
  padding: 15px;
}

.all-contributors span {
  margin: 15px 50px 15px 15px;
  font-size: 2em;
  font-style: italic;
  font-weight: 500;
}

.progress {
  background-color: #e1e1e1;
  margin-top: -45px;
}

.progress .content-stripe {
  padding-block: 100px 50px;
}

.progress .headline {
  text-align: right;
  margin-right: 20px;
}

.progress section {
  margin: 20px;
}

.feed {
  margin-bottom: 50px;
}

.feed header {
  margin-bottom: 20px;
}

.feed h3 {
  border-left: 3px solid #afafaf;
  margin-left: 10px;
  padding: 3px 12px;
  font-weight: 600;
  display: inline;
}

.feed-date {
  font-size: .95em;
}

.feed-content {
  grid-template-columns: 50px auto;
  gap: 40px;
  padding-inline: 15px;
  display: grid;
}

.milestone-tag {
  background-color: #6fb118;
  border-radius: 50%;
  outline: 4px solid #417d00f2;
  width: 50px;
  height: 50px;
  box-shadow: 2px 4px 5px gray;
}

.post {
  width: 85%;
}

.post-text {
  text-align: justify;
  color: #000;
  letter-spacing: .01em;
  line-height: 1.35em;
}

.post-multimedia {
  text-align: center;
}

.post-multimedia .video, .post-multimedia .embedded-video, .post-multimedia .gallery {
  margin: 20px;
}

.post-multimedia .video {
  position: relative;
}

.post-multimedia .video button {
  background-color: #dd1818;
  border-radius: 10px;
  width: 80px;
  height: 40px;
  margin-top: -20px;
  margin-left: -40px;
  font-size: x-large;
  line-height: 0;
  position: absolute;
  top: 50%;
  left: 50%;
}

.slideshow {
  justify-content: center;
  align-items: center;
  height: 450px;
  display: flex;
  position: relative;
}

.slideshow video, .slideshow img {
  z-index: 5;
  border: 10px solid #fff;
  max-width: 600px;
  max-height: 450px;
  box-shadow: 0 0 4px #999;
}

.slideshow .controls {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
}

.controls .arrow {
  cursor: pointer;
  align-items: center;
  width: 50px;
  height: 100%;
  display: flex;
}

.controls .arrow div {
  text-shadow: 0 0 2px #fff;
  opacity: .5;
  width: 100%;
  padding-inline: 10px;
  font-size: 30px;
  line-height: 0;
  transform: scaleY(2);
}

.controls .arrow:hover div {
  padding-inline: 0;
  animation: .9s cubic-bezier(.71, .05, 1, 1) infinite alternate bounce;
}

@keyframes bounce {
  from {
    padding-inline: 10px;
  }

  to {
    padding-inline: 0;
  }
}

.arrow-left {
  text-align: left;
}

.arrow-right {
  text-align: right;
}

.feed-control {
  text-align: center;
}

.feed-control button {
  padding: 10px 15px;
}
